.loader-film {
  width: 210px;
  height: 120px;
  background: linear-gradient(174deg, rgba(0, 0, 0, 0) 49%, #231212 50%, rgba(0, 0, 0, 0) 51%);
  position: absolute;
  top: 90px;
  left: 40%;
  transform: rotate(-90deg);
}

.loader-film:after, .loader-film:before {
  content: "";
  width: 35px;
  height: 35px;
  box-sizing: border-box;
  border: 4px dashed #fff;
  border-radius: 100%;
  animation: tape 2s linear infinite;
  display: block;
  position: absolute;
  bottom: 49px;
  transform: rotate(0deg);
}

.loader-film:before {
  right: -14px;
  box-shadow: 0 0 0 4px #fff, 0 0 0 34px #280202, 0 0 5px 34px rgba(0, 0, 0, .333);
}

.loader-film:after {
  left: -13px;
  box-shadow: 0 0 0 4px #fff, 0 0 0 65px #231212, 0 0 5px 65px rgba(0, 0, 0, .333);
}

@keyframes tape {
  0% {
    transform: rotate(0deg)scale(.4);
  }

  100% {
    transform: rotate(-360deg)scale(.4);
  }
}

.loader-word {
  letter-spacing: 2px;
  color: brown;
  text-shadow: 0 0 1px #8b4513;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: bold;
  animation: smokeOut 1s ease-in-out infinite alternate;
}

.loader-film:before {
  content: "Loading";
}

@keyframes smokeOut {
  100% {
    opacity: .08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

.js-modal-loader {
  z-index: 55;
}

.loader-lines {
  perspective: 1000px;
  width: 300px;
  height: 300px;
  color: #fff;
  border-radius: 50%;
  position: fixed;
  top: 50%;
  left: 44%;
  transform: rotateZ(45deg);
}

.loader-lines:before, .loader-lines:after {
  content: "";
  width: inherit;
  height: inherit;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateX(70deg);
}

.loader-lines:after {
  color: #ff3d00;
  animation-delay: .4s;
  transform: rotateY(70deg);
}

@keyframes rotate {
  0% {
    transform: translate(-50%, -50%)rotateZ(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotateZ(360deg);
  }
}

@keyframes rotateccw {
  0% {
    transform: translate(-50%, -50%)rotate(0deg);
  }

  100% {
    transform: translate(-50%, -50%)rotate(-360deg);
  }
}

@keyframes spin {
  0%, 100% {
    box-shadow: .2em 0;
  }

  12% {
    box-shadow: .2em .2em;
  }

  25% {
    box-shadow: 0 .2em;
  }

  37% {
    box-shadow: -.2em .2em;
  }

  50% {
    box-shadow: -.2em 0;
  }

  62% {
    box-shadow: -.2em -.2em;
  }

  75% {
    box-shadow: 0 -.2em;
  }

  87% {
    box-shadow: .2em -.2em;
  }
}

